#role {
  background-color: rgb(9, 110, 110);
  border-color: tan;
  border-radius: 5px;
  width: 10rem;
  height: 4rem;
}


.role-description {
  padding:50px;
}

.role-description p {
  color: black;
  font-family: 'Monaco';
  font-size: 12px;
}

p a {
    color:white;
    font-size: 12px;
}

.effect-shine {
  background-image: -webkit-gradient(linear, left top, right top, color-stop(25%, white), color-stop(37.5%, #fde071), color-stop(#ef9671), color-stop(62.5%, #996a6e), color-stop(75%, white));
  background-image: linear-gradient(90deg, white 25%, #fde071 37.5%, #ef9671, #996a6e 62.5%, white 75%);
  background-position: right center;
  background-size: 400% auto;
  -webkit-background-clip: text;
  background-clip: text;
  color: white;
  -webkit-text-fill-color: transparent;
  -webkit-transition: color 200ms linear;
  transition: color 200ms linear;
}

.effect-shine:hover {
  background-position: left center;
  color: #ef9671;
  -webkit-transition: background-position 2000ms ease-out;
  transition: background-position 2000ms ease-out;
}

@media (max-width: 786px) {
  #role {
  background-color: rgb(43, 96, 196);
  }
}
#about {
    background: -webkit-gradient( linear,
    left top, left bottom,
    from(rgba(1, 7, 7, 0.897)),
    to(rgba(0, 0, 0, 0.897)) ),
     url(/static/media/highrise.8d7cafea.jpg);
    background: linear-gradient( rgba(1, 7, 7, 0.897),
    rgba(0, 0, 0, 0.897) ),
     url(/static/media/highrise.8d7cafea.jpg);
     background-size:100% 100%;
     background-position: relative;
     background-attachment: fixed;
    display: flex;
    flex-direction: column;
    min-height: 85vh;
    padding: 20px; 
}

#about h2 {
    margin-top:3rem;
}
.ui.container.about {
    padding: 30px;
    display:flex;
    justify-content: center;
    align-items: center;
}

.about-header h2{
    color:white;
    text-shadow: 3px 3px black;
    font-family: royal crescent;
    padding:10px;
}

.about-image h5{
    font-family: 'Cochin';
    padding: 5px;
}

.about-image img{
    border: 0.2rem;
    border-radius: 0.5rem;
    opacity: 0.7;
}

.about-image img:hover{
    opacity: 1;
}

.about-p p {
    font-family: 'Cochin';
    font-size: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding:20px;
}

.even {
    flex-direction: row-reverse;
}

@media (max-width: 768px) {
    .about {
        display:flex;
        justify-content: center;
        flex-wrap: inherit;
    }
    .about-image {
        padding: 20px 40px 10px 40px;
        display:flex; 
        flex-direction: column;
        align-items: center;
    }
    .about-p p {
        display:flex;
        padding: 20px;
    }
}
